import gql from 'graphql-tag'

export const LOGIN_QUERY = gql(`
      query Login($email: String!, $password: String!) {
        login(email: $email, password: $password) {
          token
          name
          email
          id
          company 
          role
          country
          internal_user
          cloud_backend
          cloud_password
          cloud_port
          review_modal_shown
        }
      }`)

export const SOCIAL_LOGIN_QUERY = gql(`
      query social_login($provider: String!, $token: String!) {
        socialLogin(provider: $provider, token: $token) {
            res_message
            status
            user {
                token
                name
                email
                id
                company 
                role
                country
                internal_user
                cloud_backend
                cloud_password
                cloud_port
                review_modal_shown
            }
        }
      }`)

export const GET_USER_DETAILS = `
      query as($id:Int) {
        users(id:$id){
          experience
          task
          found
        }
      }`

export const SIGNUP_QUERY = gql(`
    query Signup($username : String!, $email: String!, $password: String!) {
      signup(username: $username, email: $email, password: $password) {
        name
        email
        id
      }
    }`)

export const USER_ONBOARD_MUTATION = `
    mutation updateUserInfo($name: String!, $company: String!, $role: String!, $country: String!, $experience: String!, $task: String!, $found: String!) {
      userUpdateDetails(name: $name, company: $company, role: $role, country: $country, experience: $experience, task: $task, found: $found) {
        id
        name
        company
        role
        country
        status
      }
    }
`

export const USER_UPDATE_DETAILS_MUTATION = `
    mutation updateUserInfo($name: String!, $company: String!, $role: String!, $country: String!) {
      userUpdateDetails(name: $name, company: $company, role: $role, country: $country) {
        id
        name
        company
        role
        country
        status
      }
    }
`

export const SIGNUP_MUTATION = `
    mutation addUser($email: String!, $name: String!, $password: String!, $company: String!, $country: String!, $role: String!, $campaign: String!, $language: String!) {
      userCreate(email: $email, name: $name, password: $password, company: $company, country:$country , role:$role, campaign:$campaign, language:$language) {
        id
        name
        status
      }
    }
`

export const SOCIAL_SIGNUP_MUTATION = `
    mutation socialSignup($provider: String!, $token: String!, $campaign: String!, $language: String!) {
        socialSignup(provider: $provider, token: $token, campaign: $campaign, language: $language) {
        res_message
        status
        user {
            token
            name
            email
            id
            company 
            role
            country
            cloud_backend
            cloud_password
            cloud_port
        }
      }
    }
`

export const PASSWORD_REQUEST_QUERY = `
  query resetPassword($email: String!) {
    resetPassword(email: $email, type: "request_token") {
      response
    }
  }`

export const UPDATE_EMAIL_PASSWORD_MUTATION = `
  mutation userUpdateEmailPassword($email: String!, $password: String!, $new_password: String!) {
    userUpdateEmailPassword(email: $email, password: $password, new_password: $new_password) {
      status
    }
  }`

export const GET_USER_DETAILS_INIT = gql(`
      query as($id: Int) {
          users(id: $id) {
            id
          }
        }`)

export const USER_ALLOWED_API_ACCESS = `
  query userAllowedAPIAccess($id:Int) {
    userAllowedAPIAccess(id: $id) {
      allowed_api_access
    }
  }`

export const USER_UPDATE_REVIEW_MODAL_SHOWN_MUTATION = `
  mutation updateUserReviewModalShown($review_modal_shown: Boolean!) {
    updateUserReviewModalShown(review_modal_shown: $review_modal_shown) {
      id
    }
  }`

export const GET_USER_EXTERNAL_API_KEYS = `
  query userExternalAPIKeys($services:String) {
    userExternalAPIKeys(services: $services){
      key_values
    }
  }`

export const UPDATE_USER_EXTERNAL_API_KEYS = `
  mutation updateUserExternalAPIKeys($external_api_keys:String) {
    updateUserExternalAPIKeys(external_api_keys: $external_api_keys) {
      status
    }
  }`
